* {
  color: #F7F5D1;
  font-family: "Raleway", Verdana, sans-serif;
  padding: 0;
  margin: 0;
}

body {
  font-size: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(44,149,133,0.77)),
    to(rgba(2,82,70,0.77))
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: -webkit-linear-gradient(
    left,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: -o-linear-gradient(
    left,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: linear-gradient(
    to right,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background-size: cover;

  overflow-y: auto;
  height: 100%;

}



